import React, { useEffect, useState } from 'react';
import 'braintree-web';
import axios from 'axios';
import DropIn from 'braintree-web-drop-in-react';

function Landing() {
    const [braintreeToken, setBraintreeToken] = useState('');

    useEffect(() => {
        axios.get('http://localhost:3001/api/braintree/v1/getToken')
            .then(response => {
                setBraintreeToken(response.data.clientToken);
                // console.log(response.data.clientToken);
            })
            .catch(err => console.error(err))
    }, []);

    let braintreeInstance;
    const buy = async () => {
        try {
            const { nonce } = await braintreeInstance.requestPaymentMethod();
            console.log("nonce====", nonce);
        } catch (error) {
            console.log(error);
        }
    }

    if (!braintreeToken) {
        return (
            <div>
                <h1>Loading...</h1>
            </div>
        )
    }
    return (
        <div className='flex flex-center'>
            <div className='card'>
                {/* <div className="card-title">Braintree Token: </div>
                <p style={{ marginTop: '1rem' }}>{braintreeToken}</p> */}
                <DropIn options={{
                    authorization: braintreeToken
                }}
                    onInstance={instance => (braintreeInstance = instance)} />
                <button className='btn' onClick={buy}>Buy</button>
            </div>

        </div>
    )
}

export default Landing;
